html {
  position: relative;
  min-height: 100%;
}
body {
  margin-bottom: 50px; /* Margin bottom by footer height */
}
.footer {
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 50px;
    line-height: 20px;
    background-color: #f5f5f5;
    display: block;
}



